<template>
  <a-spin :spinning="loading">
    <div class="setting">
      <h1>基本信息</h1>
      <div style="padding: 0 20px;">
        <a-form
          layout="inline"
          :label-col="{md: 2, xs: 3}"
          labelAlign="right"
        >
          <a-form-item label="账号">
            {{ params.phone }}
          </a-form-item>
          <a-form-item label="店铺名称">
            <a-input v-model="params.name" />
          </a-form-item>
          <a-form-item label="店铺简介">
            <a-input type="textarea" v-model="params.introduction" />
          </a-form-item>
          <a-form-item label="Logo">
            <a-upload
              @change="handleChange"
              :show-upload-list="false"
              :action="`${uploadPrefix}/upload`"
            >
              <div
                v-if="params.logo"
                class="bg_img"
                :style="`background-image: url(${imgPrefix}${params.logo}); width: 148px; height: 148px;`"
              ></div>
              <div class="upload_text" v-else>
                <a-icon :type="iconLoading ? 'loading' : 'plus'" />
                <div class="ant-upload-text">
                  Upload
                </div>
              </div>
            </a-upload>
          </a-form-item>
        </a-form>

        <div>
          <a-button type="primary" @click="submit" :loading="submitLoading">保存</a-button>
        </div>
      </div>
    </div>
  </a-spin>
</template>
<script>
import { mapActions, mapState } from 'vuex'
export default {
  name: 'Setting',
  data() {
    let params = {
      name: '',
      phone: '',
      logo: '',
      introduction: ''
    }
    return {
      params,
      logoImg: '',
      loading: false,
      iconLoading: false,
      sex: 1,
      isEditPhone: false,
      phone: '',
      submitLoading: false,
      proxy: process.env.VUE_APP_API_URL,
      uploadPrefix: process.env.VUE_APP_UPLOAD_PREFIX,
      prefix: process.env.VUE_APP_PREFIX,
      imgPrefix: process.env.VUE_APP_IMG_PREFIX,
    }
  },
  computed: {
    ...mapState('shop', ['detail']),
  },
  methods: {
    ...mapActions('shop', {
      getShopDetail: 'getDetail',
      updateShop: 'update'
    }),
    handleChange(file) {
      if (file.file.status === 'uploading') {
        this.params.logo = ''
        this.iconLoading = true
      }
      if (file.file.status === 'done') {
        this.params.logo = file.file.response.data
        this.iconLoading = false
      }
    },
    submit() {
      this.submitLoading = true
      this.updateShop({
        id: this.params.id,
        data: this.params
      }).then(res => {
        this.$message.success('修改成功')
      }).finally(() => {
        this.submitLoading = false
      })
    },
    echoData() {
      const shop_id = Number(sessionStorage.getItem('shop_id'))
      if (!shop_id) {
        return ;
      }
      this.loading = true
      this.getShopDetail(shop_id).then(res => {
        this.params = Object.assign({}, this.params, res.data)
      }).finally(() => {
        this.loading = false
      })
    }
  },
  created() {
    this.echoData()
  }
}
</script>
<style lang="scss" scoped>
.ant-form {
  .ant-form-item {
    width: 100%;
    margin-bottom: 20px;
  }
}
.ant-upload-select-text {
  .upload_text {
    width: 128px;
    height: 128px;
    border: 1px dashed #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.ant-input {
  width: 200px;
}
</style>
